
import { defineComponent, PropType } from "vue";
import TaskBox from './TaskBox.vue';
import Stopwatch from './Stopwatch.vue';
import ITask from '../interfaces/ITask';

export default defineComponent({
  name: "Task",

  components: {
    TaskBox,
    Stopwatch
  },

  props: {
    task: {
      default: () => ({}),
      required: true,
      type: Object as PropType<ITask>
    }
  }
});
